import { Component, OnInit, NgModule, ViewChild, Injectable, Inject } from '@angular/core';
import { NgbModal, NgbActiveModal, NgbTypeahead } from '@ng-bootstrap/ng-bootstrap';
import { HttpClient } from '@angular/common/http';
import { Observable, Subject, merge } from 'rxjs/index';
import { debounceTime, distinctUntilChanged, filter, map } from 'rxjs/operators';

@Injectable()
export class LojasService {
  private baseUrl;
  constructor(@Inject('BASE_URL') baseUrl: string, private http: HttpClient) {
    this.baseUrl = baseUrl;
  }

  getLojas(): Observable<any> {
    return this.http.get('./assets/lojas.json');
  }

  setLoja(filial: string, armazem: string): Observable<any> {
    return this.http.post(this.baseUrl + 'api/invent/SetFilial?filial=' + filial + '&armazem=' + armazem, "");
  }
}

@Component({
  selector: 'setup',
  templateUrl: './setup.component.html',
  styleUrls: ['./setup.component.css'],
  providers: [NgbModal, LojasService]
})
/** setup component*/
export class SetupComponent implements OnInit {

  @ViewChild('instance') instance: NgbTypeahead;

  public lojas: any;
  public lojaSelect: loja;

  focus$ = new Subject<string>();
  click$ = new Subject<string>();

  /** setup ctor */
  constructor(public activeModal: NgbActiveModal, private lojaService$: LojasService) { }

  ngOnInit() {
    this.lojaService$.getLojas()      
      .subscribe(
      res => { this.lojas = res.lojas as loja[], console.log(res as loja[]) },
        error => console.log(error));
  }


  search = (text$: Observable<string>) => {
    const debouncedText$ = text$.pipe(debounceTime(200), distinctUntilChanged());
    const clicksWithClosedPopup$ = this.click$.pipe(filter(() => !this.instance.isPopupOpen()));
    const inputFocus$ = this.focus$;

    return merge(debouncedText$, inputFocus$, clicksWithClosedPopup$).pipe(
      map(term => (term === '' ? this.lojas.slice(0,10)
        : this.lojas.filter(v => v.loja.toLowerCase().indexOf(term.toLowerCase()) > -1).slice(0,10)))
    );
  }

  formatter = (x: { loja: string }) => x.loja;

  setLoja() {
    if (this.lojaSelect !== undefined && this.lojaSelect.loja !== undefined) {
      if (confirm("Confirma a seleção da loja: " + this.lojaSelect.loja + "?")) {
        this.lojaService$.setLoja(this.lojaSelect.filial, this.lojaSelect.armazem).subscribe(
          res => console.log(res),
          err => console.log(err)
        );

        this.activeModal.close();
      }
    } else {
      alert("Você deve selecionar uma loja antes de salvar");
    }
  }
}

interface loja {
  filial: string;
  armazem: string;
  loja: string;
}
