import { Component, Inject, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Invent } from '../inventario/inventario.component';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-impressao-modal',
  templateUrl: './impressao-modal.component.html',
  styleUrls: ['./impressao-modal.component.css']
})
/** ImpressaoModal component*/
export class ImpressaoModalComponent implements OnInit {

  public inventario: Invent;
  private baseUrl: string;  
  constructor(private http: HttpClient, @Inject('BASE_URL') baseUrl: string, public activeModal: NgbActiveModal) {
    this.baseUrl = baseUrl;
  }

  ngOnInit(): void {
    const method: string = 'api/invent/prodmovest';

    this.http.get<Invent>(this.baseUrl + method).subscribe((result) => {
      this.inventario = result;
    }, error => console.error(error),
      () => this.imprimir());
  }

  async delay(ms: number) {
    return new Promise<void>(function (resolve) {
      setTimeout(resolve, ms);
    });
  }

  async imprimir() {
    await this.delay(500);

    var divToPrint = document.getElementById("printTable");
    const newWin = window.open("");
    newWin.document.write(divToPrint.outerHTML);
    newWin.print();
    newWin.close();
    this.activeModal.close();
  }
}
