import { BrowserModule } from '@angular/platform-browser';
import { NgModule, LOCALE_ID } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { RouterModule } from '@angular/router';
import { NgbModule, NgbActiveModal, NgbDateParserFormatter } from '@ng-bootstrap/ng-bootstrap';

import { AppComponent } from './app.component';
import { NavMenuComponent } from './nav-menu/nav-menu.component';
import { HomeComponent } from './home/home.component';
import { SetupComponent } from './setup/setup.component';
import { InventarioComponent, FilterPipe } from './inventario/inventario.component';
import { DataTransferService } from './inventario/data-transfer.service';
import { InventEmbModalComponent } from './invent-emb-modal/invent-emb-modal.component';

import { registerLocaleData } from '@angular/common';
import localePt from '@angular/common/locales/pt';
import { ErrosModalComponent } from './erros-modal/erros-modal.component';
import { ImpressaoModalComponent } from './impressao-modal/impressao-modal.component';
registerLocaleData(localePt);

@NgModule({
  declarations: [
    AppComponent,
    NavMenuComponent,
    HomeComponent,
    SetupComponent,
    InventarioComponent,
    InventEmbModalComponent,
    ErrosModalComponent,
    ImpressaoModalComponent,
    FilterPipe
  ],
  imports: [
    BrowserModule.withServerTransition({ appId: 'ng-cli-universal' }),
    HttpClientModule,
    FormsModule,    
    NgbModule.forRoot(),
    RouterModule.forRoot([
      { path: '', component: HomeComponent, pathMatch: 'full' },
      { path:'inventario', component: InventarioComponent},
      {path:'setup', component: SetupComponent}
    ])
  ],
  providers: [DataTransferService, { provide: LOCALE_ID, useValue: 'pt-BR' }, NgbActiveModal],
  exports: [FilterPipe],
  bootstrap: [AppComponent], entryComponents: [InventEmbModalComponent, ErrosModalComponent, ImpressaoModalComponent]
})
export class AppModule { }
