import { Component, Input, OnInit } from '@angular/core';

import { NgbModal, NgbActiveModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { itemInvent } from '../inventario/inventario.component';
import { DataTransferService } from '../inventario/data-transfer.service';

@Component({
  selector: 'app-invent-emb-modal',
  templateUrl: './invent-emb-modal.component.html',
  styleUrls: ['./invent-emb-modal.component.css']
})
/** InventEmbModal component*/
export class InventEmbModalComponent implements OnInit {

  public invItemInv: itemInvent;

  constructor(public activeModal: NgbActiveModal, private data: DataTransferService) {

  }

  public ngOnInit(): void {
    this.data.currentInvItem.subscribe(item => this.invItemInv = item);
  }

  atualizaCusto(): void {
    this.invItemInv.qtdProtheus = this.invItemInv.qtdConv1 * this.invItemInv.conversor1 +
      this.invItemInv.qtdConv2 * this.invItemInv.conversor2 +
      this.invItemInv.qtdConv3 * this.invItemInv.conversor3;
    this.invItemInv.custo = this.invItemInv.qtdProtheus * this.invItemInv.valorUnitario;
  }

  btnSave() {
    let isToClose = true;
    const percDiff = (this.invItemInv.saldoEstoque - this.invItemInv.qtdProtheus) / this.invItemInv.saldoEstoque * -100;
    if (percDiff <= -15 || percDiff >= 15) {
      isToClose = confirm('A divergência do estoque sistêmico e o inventariado é de ' + percDiff.toFixed(2) + '%, Confirma esta quantidade?');
    }
    if (isToClose) {
      this.activeModal.close();
    }
  } 
}

  
