import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { itemInvent } from './inventario.component';

@Injectable()
export class DataTransferService {

  private invIdSource = new BehaviorSubject(0);
  public currentInvId = this.invIdSource.asObservable();

  private itemInv = <itemInvent>{}
  private invItemSource = new BehaviorSubject(this.itemInv);
  public currentInvItem = this.invItemSource.asObservable();

  constructor() { }

  public changeInvId(invId: number) {
    this.invIdSource.next(invId);
  }

  public changeInvItem(invItem: itemInvent) {
    this.invItemSource.next(invItem);
  }
}

