import { Component, Inject, OnInit, Pipe, Injectable, PipeTransform } from '@angular/core';
import { ActivatedRoute, Router } from "@angular/router";
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { DataTransferService } from '../inventario/data-transfer.service';
import { NgbModal, NgbActiveModal, NgbDateParserFormatter, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { InventEmbModalComponent } from '../invent-emb-modal/invent-emb-modal.component';

//filtar produto
@Pipe({
  name: 'filter',
})
@Injectable()
export class FilterPipe implements PipeTransform {
  transform(items: any[], field: string, value: string): any[] {
    if (!items) {
      return [];
    }
    if (!field || !value) {
      return items;
    }

    return items.filter(singleItem =>
      singleItem[field].toLowerCase().includes(value.toLowerCase())
    );
  }
}

@Component({
  selector: 'app-inventario',
  templateUrl: './inventario.component.html',
  styleUrls: ['./inventario.component.css'],
})
/** inventario component*/
export class InventarioComponent implements OnInit {

  public invId: number;
  public inventario: Invent;
  public vlrTotal: number;
  public meses: iMeses[];
  public mes: iMeses;
  public searchString: string;
  public gravarStat: boolean = false;

  private itemInv: itemInvent;
  private baseUrl: string;

  constructor(private http: HttpClient, @Inject('BASE_URL') baseUrl: string, private modalService: NgbModal, private data: DataTransferService, private router: Router, private route: ActivatedRoute) {
    this.baseUrl = baseUrl;
    this.vlrTotal = 0;
    this.meses = [{ id: 0, nome: "Selecione o Mês" }, { id: 1, nome: "Janeiro" }, { id: 2, nome: "Fevereiro" }, { id: 3, nome: "Março" }, { id: 4, nome: "Abril" }, { id: 5, nome: "Maio" }, { id: 6, nome: "Junho" }, { id: 7, nome: "Julho" }, { id: 8, nome: "Agosto" }, { id: 9, nome: "Setembro" }, { id: 10, nome: "Outubro" }, { id: 11, nome: "Novembro" }, { id: 12, nome: "Dezembro" }];
    this.mes = this.meses[0];
  }
  ngOnInit(): void {
    this.data.currentInvId.subscribe(id => this.invId = id);
    this.data.currentInvItem.subscribe(item => this.itemInv = item);    
    this.loadData();
  }

  loadData(): void {
    let method: string;
    if (this.invId === 0) {
      method = 'api/invent/prodmovest';
    } else {
      method = 'api/invent/ItensInvent/' + this.invId;
    }

    this.http.get<Invent>(this.baseUrl + method).subscribe((result) => {
      this.inventario = result;
      const mes: number = +this.inventario.data.substr(3, 2);
      this.mes = this.meses[mes];
    }, error => console.error(error),//tratamento de erro
      () => this.updateVars());//chamada quando a funcao estiver concluida
  }

  updateVars(): void {
    this.vlrTotal = 0;

    for (let it of this.inventario.itens) {
      this.vlrTotal += it.custo = it.qtdProtheus * it.valorUnitario;
    }
  }
  //Fix the confirm lack causing refocus
  setCurrent(event: any): void {
    event.srcElement.setAttribute('origValue', event.target.value);
  } 

  setValor(itemInv: itemInvent ): boolean {
    let index = this.inventario.itens.indexOf(itemInv);
    let item = itemInv;
    const qtd = item.qtdProtheus;
    const orig = +(event.srcElement.getAttribute('origValue'));
    let percDiff = (item.saldoEstoque !== 0) ? (item.saldoEstoque - qtd) / item.saldoEstoque * -100 : 100;    

    if (qtd != orig) {
      if ((percDiff <= -15 || percDiff >= 15) && qtd !== 0) {
        if (confirm('A divergência do estoque sistêmico e o inventariado é de ' + percDiff.toFixed(2) + '%, Confirma esta quantidade?')) {
          this.inventario.itens[index].qtdProtheus = qtd;
          this.inventario.itens[index].qtdConv1 = 0;
          this.inventario.itens[index].qtdConv2 = 0;
          this.inventario.itens[index].qtdConv3 = this.inventario.itens[index].qtdProtheus;

          this.inventario.itens[index].custo = this.inventario.itens[index].qtdProtheus * this.inventario.itens[index].valorUnitario;

          this.updateVars();
        }
      }
    }

    return true;
  }

  detailEdit(produtoId: number): void {
    let itemInv: itemInvent = this.inventario.itens.find(x => x.produtoId == produtoId);
    let index = this.inventario.itens.findIndex(x => x.produtoId == produtoId);
    this.data.changeInvItem(itemInv);
    const modal = this.modalService.open(InventEmbModalComponent, { size: 'lg' });
    modal.result.then(() => { this.updateVars(); }, () => { console.log('Backdrop click') });
  }

  saveInv(): void {
    if (this.mes === undefined || this.mes.id === 0) {
      alert("O mês deve ser informado");
      return;
    }

    this.gravarStat = true; //desabilita o botão
    const now = new Date(Date.now());
    const dtInv = new Date(((this.mes.id === 12 && now.getMonth() === 0) ? now.getFullYear() - 1 : now.getFullYear()), this.mes.id, 0);
    this.inventario.data = `${dtInv.getFullYear()}-${this.mes.id.toString().padStart(2, '0')}-${dtInv.getDate()}`;

    this.fixItens(); //preenche com zero os campos deixados em branco

    const json = JSON.stringify(this.inventario);

    const head = new HttpHeaders({ 'Content-Type': 'application/json' });

    let metodo: string;
    if (this.invId === 0) {
      metodo = 'api/invent/IncInvent';
      this.http.post(this.baseUrl + metodo, json, { headers: head }).subscribe((result) => console.info(result), error => console.log(error), () => { this.router.navigate(['/']) });
    } else {
      metodo = 'api/invent/AltInvent/';
      this.http.put(this.baseUrl + metodo, json, { headers: head }).subscribe((result) => console.info(result), error => console.log(error), () => { this.router.navigate(['/']) });
    }
  }

  fixItens(): void {
    this.inventario.itens.forEach((val, idx, arr) => {
      if (val.qtdConv1 == undefined) {
        val.qtdConv1 = 0;
      }
      if (val.qtdConv2 == undefined) {
        val.qtdConv2 = 0;
      }
      if (val.qtdConv3 == undefined) {
        val.qtdConv3 = 0;
      }
      if (val.qtdProtheus == undefined) {
        val.qtdProtheus = 0;
      }
    });

    this.updateVars();
  }
}

export interface Invent {
  id: number,
  data: string,
  documento: string,
  itens: itemInvent[]
}

export interface itemInvent {
  produtoId: number;
  codigo: string;
  descricao: string;
  conversor1: number;
  conversor2: number;
  conversor3: number;
  descConversor1: string;
  descConversor2: string;
  descConversor3: string;
  qtdConv1: number;
  qtdConv2: number;
  qtdConv3: number;
  qtdProtheus: number;
  saldoEstoque: number;
  valorUnitario: number;
  custo: number;
  um: string;
  edit: boolean;
}

interface iMeses {
  id: number;
  nome: string;
}
