import { Component, Inject, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { DataTransferService } from '../inventario/data-transfer.service';
import { Router, ActivatedRoute } from '@angular/router';
import { NgbModal, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { ErrosModalComponent } from '../erros-modal/erros-modal.component';
import { SetupComponent } from '../setup/setup.component';
import { ImpressaoModalComponent } from '../impressao-modal/impressao-modal.component';
import { error } from 'protractor';
import { timeInterval, switchMap, concatMap } from 'rxjs/operators';
import { BehaviorSubject, timer, Observable, interval } from 'rxjs';
import { forEach } from '@angular/router/src/utils/collection';
import { Response } from 'selenium-webdriver/http';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
})
export class HomeComponent implements OnInit {

  public cabInventarios: CabecInventario[];
  public loja;
  public ultSync: string = '';
  private baseUrl: string;
  private invId: number;
  private isSetted: boolean = false;  
  private ultSyncGetter$;  

  constructor(private http: HttpClient, @Inject('BASE_URL') baseUrl: string, private data: DataTransferService, private router: Router, private modalService: NgbModal, private route: ActivatedRoute) {
    this.baseUrl = baseUrl;
  }

  ngOnInit(): void {
    this.data.currentInvId.subscribe(id => this.invId = id);
    this.checkSettings();
    this.getUltSync();
  }

  private checkSettings() {
    this.http.get<boolean>(this.baseUrl + 'api/invent/setted').subscribe((result: any) => {
      this.isSetted = result.setted;      
    }, error => console.log(error),
      () => {        
        if (this.isSetted) {
          this.loadData();
          if (this.loja === undefined) {
            this.getLoja();
          } 
        } else {
          this.firstRun();
        }
      });
  }

  private loadData() {
    this.http.get<CabecInventario[]>(this.baseUrl + 'api/invent/CabecInvent').subscribe(result => {
      this.cabInventarios = result;
    }, error => console.error(error));
  }

  printInv(): void {
    this.modalService.open(ImpressaoModalComponent);
  }

  novoInv() {
    this.data.changeInvId(0);
    this.router.navigate(['/inventario']);
  }

  altInv(id: number) {
    this.data.changeInvId(id);
    this.router.navigate(['/inventario']);
  }

  firstRun() {
    const modal = this.modalService.open(SetupComponent, { backdrop: 'static', keyboard: false });
    modal.result.then(() => { this.loadData() });
  }

  showErrors(invId: number) {
    const erros = this.cabInventarios.find(f => f.id == invId).errorMessage;
    const doc = this.cabInventarios.find(f => f.id == invId).documento;

    const modal = this.modalService.open(ErrosModalComponent);
    modal.componentInstance.title = 'Erros do Inventario: ' + doc;
    modal.componentInstance.erros = erros;
  }

  finInv(invId: number) {
    this.http.post(this.baseUrl + 'api/invent/FinInv/' + invId.toString(), "").subscribe((result) => {
      console.log(result);
    }, error => { console.error(error), this.loadData() }
      , () => this.loadData());
  }

  exportExcel(invId: number) {
    //this.http.get<Response>(this.baseUrl + 'api/invent/exportexcel/' + invId.toString());
    window.location.href = this.baseUrl + 'api/invent/exportexcel/' + invId.toString();
  }

  getUltSync() {
    this.ultSyncGetter$ = timer(0, 5000).subscribe(() => {
      this.http.get(this.baseUrl + 'api/invent/ultSync').subscribe(
        (dt: any) => this.ultSync = dt.ultSync,
        err => console.log(err));
    });
  }

  getLoja() {
    
    this.http.get<loja>(this.baseUrl + 'api/invent/loja').subscribe(
      (lj) => {

        this.http.get<lojas>('./assets/lojas.json').subscribe(
          (ljs: lojas) => {
            ljs.lojas.forEach(element => {
              if (element.armazem == lj.armazem && element.filial == lj.filial) {
                this.loja = element.loja + " - Armazém: " + element.armazem + " - Filial: " + element.filial;
              }
            });
          },
          err => console.log(err));

      },
      err => console.log(err));

    
    }
}

interface CabecInventario {
  id: number,
  dataFormatted: string,
  status: string,
  hasError: boolean,
  errorMessage: string,
  documento: string,
  retry: string,
  qtdSync: number,
  lock: string
}

interface lojas {
  lojas: loja[];
}

interface loja {
  filial: string;
  armazem: string;
  loja: string;
}
